import PropTypes from "prop-types";
import { useState } from "react";
import { format } from "date-fns";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  TableRow,
  Checkbox,
  TableCell,
  Typography,
  MenuItem,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button,
  Stack,
  TextField,
  CircularProgress,
} from "@mui/material";
// utils
import { fCurrency } from "../../../../utils/formatNumber";
import { useDispatch } from "../../../../redux/store";
import {
  updateAgentSuccess,
  handleDepositAgent,
  handleDepositAgentHtg,
  handleWithdrawAgent,
  handleWithdrawAgentHtg
} from "../../../../redux/slices/agent";
// components
import Label from "../../../../components/Label";
import Image from "../../../../components/Image";
import Iconify from "../../../../components/Iconify";
import { TableMoreMenu } from "../../../../components/table";
import { useTranslation } from "react-i18next";
//

// ----------------------------------------------------------------------

AgentTableRow.propTypes = {
  row: PropTypes.object,
  selected: PropTypes.bool,
  onEditRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onDeleteRow: PropTypes.func,
  deleteText: PropTypes.string,
  editText: PropTypes.string,
  depositText: PropTypes.string,
  withdrawText: PropTypes.string,
};

export default function AgentTableRow({
  row,
  selected,
  onEditRow,
  onSelectRow,
  onDeleteRow,
  deleteText = "Delete",
  editText = "Edit",
  depositText = "Deposit",
  withdrawText = "Withdraw",
}) {
  const theme = useTheme();

  const dispatch = useDispatch();

  const [openMenu, setOpenMenuActions] = useState(null);
  const [amount, setAmount] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  const { t } = useTranslation();

  const [openDeposit, setOpenDeposit] = useState(false);
  const [openDepositHtg, setOpenDepositHtg] = useState(false);

  const handleCloseDeposit = () => {
    setOpenDeposit(false);
  };

  const handleClickOpenDeposit = () => {
    setOpenDeposit(true);
  };

  const handleCloseDepositHtg = () => {
    setOpenDepositHtg(false);
  };

  const handleClickOpenDepositHtg = () => {
    setOpenDepositHtg(true);
  };

  const handleDeposit = async (id) => {
    if (!amount) {
      alert("Avant de recharger, entrer le montant svp.");
      return;
    }

    setLoading(true);
    try {
      const values = {
        user_id: id,
        amount,
      };
      const res = await handleDepositAgent(values);
      dispatch(updateAgentSuccess(res.user));
      setAmount(null);
      setOpenDeposit(false);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };
  const handleDepositHtg = async (id) => {
    if (!amount) {
      alert("Avant de recharger, entrer le montant svp.");
      return;
    }

    setLoading(true);
    try {
      const values = {
        user_id: id,
        amount,
      };
      const res = await handleDepositAgentHtg(values);
      dispatch(updateAgentSuccess(res.user));
      setAmount(null);
      setOpenDepositHtg(false);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const [openWithdraw, setOpenWithdraw] = useState(false);
  const [openWithdrawHtg, setOpenWithdrawHtg] = useState(false);

  const handleCloseWithdraw = () => {
    setOpenWithdraw(false);
  };

  const handleClickOpenWithdraw = () => {
    setOpenWithdraw(true);
  };

  const handleCloseWithdrawHtg = () => {
    setOpenWithdrawHtg(false);
  };

  const handleClickOpenWithdrawHtg = () => {
    setOpenWithdrawHtg(true);
  };

  const handleWithdraw = async (id) => {
    if (!amount) {
      alert("Avant de faire un retrait, entrer le montant svp.");
      return;
    }

    setLoading(true);
    try {
      const values = {
        user_id: id,
        amount,
      };
      const res = await handleWithdrawAgent(values);
      dispatch(updateAgentSuccess(res.user));
      setAmount(null);
      setOpenWithdraw(false);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleWithdrawHtg = async (id) => {
    if (!amount) {
      alert("Avant de faire un retrait, entrer le montant svp.");
      return;
    }

    setLoading(true);
    try {
      const values = {
        user_id: id,
        amount,
      };
      const res = await handleWithdrawAgentHtg(values);
      dispatch(updateAgentSuccess(res.user));
      setAmount(null);
      setOpenWithdrawHtg(false);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox">
        <Checkbox checked={selected} onClick={onSelectRow} />
      </TableCell>

      {/* <TableCell padding="checkbox" /> */}

      <TableCell align="center">{row.full_name}</TableCell>
      <TableCell align="center">USD {fCurrency(row.balance)}</TableCell>
      <TableCell align="center">HTG {fCurrency(row.balance_htg)}</TableCell>
      <TableCell align="center">{row.supervisor_name || "-"}</TableCell>
      <TableCell align="center">{row.phone || "-"}</TableCell>
      <TableCell align="center">{t(`country.${row.country_name}`)}</TableCell>
      <TableCell align="center">{row.city}</TableCell>
      <TableCell align="center">{row.address}</TableCell>

      <TableCell align="center">
        <Label
          variant={theme.palette.mode === "light" ? "ghost" : "filled"}
          color={row.is_block ? "error" : "success"}
          sx={{ textTransform: "capitalize" }}
        >
          {row.is_block ? t("params.yes") : t("params.no")}
        </Label>
      </TableCell>

      <TableCell align="center">
        <Button
          variant="contained"
          color="primary"
          size="small"
          onClick={handleClickOpenDeposit}
        >
          {depositText}
        </Button>
      </TableCell>

      <TableCell align="right">
        <TableMoreMenu
          open={openMenu}
          onOpen={handleOpenMenu}
          onClose={handleCloseMenu}
          actions={
            <>
              <MenuItem
                onClick={() => {
                  handleCloseMenu();
                  onDeleteRow();
                }}
                sx={{ color: "error.main" }}
              >
                <Iconify icon={"eva:trash-2-outline"} />
                {deleteText}
              </MenuItem>
              <MenuItem
                onClick={() => {
                  onEditRow();
                  handleCloseMenu();
                }}
              >
                <Iconify icon={"eva:edit-fill"} />
                {editText}
              </MenuItem>
              <MenuItem
                onClick={() => {
                  handleClickOpenDeposit();
                  handleCloseMenu();
                }}
                sx={{ color: "green" }}
              >
                <Iconify icon={"mdi:cash-plus"} />
                {depositText}
              </MenuItem>
              <MenuItem
                onClick={() => {
                  handleClickOpenDepositHtg();
                  handleCloseMenu();
                }}
                sx={{ color: "#ff8612" }}
              >
                <Iconify icon={"mdi:cash-plus"} />
                {depositText} HTG
              </MenuItem>

              <MenuItem
                onClick={() => {
                  handleClickOpenWithdraw();
                  handleCloseMenu();
                }}
                sx={{ color: "red" }}
              >
                <Iconify icon={"mdi:cash-minus"} />
                {withdrawText}
              </MenuItem>

              <MenuItem
                onClick={() => {
                  handleClickOpenWithdrawHtg();
                  handleCloseMenu();
                }}
                sx={{ color: "#b80d57" }}
              >
                <Iconify icon={"mdi:cash-minus"} />
                {withdrawText} HTG
              </MenuItem>
            </>
          }
        />
      </TableCell>
      <Dialog
        open={openDeposit}
        // onClose={handleCloseDeposit}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">
          Deposit : {row.full_name} !
        </DialogTitle>
        <DialogContent>
          <Stack spacing={2} sx={{ pt: 2 }}>
            <TextField
              value={amount}
              onChange={(event) => setAmount(event.target.value)}
              placeholder="Enter amount"
            />
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleCloseDeposit}
            color="primary"
            disabled={loading}
          >
            Annuler
          </Button>
          <Button
            onClick={() => handleDeposit(row.uid)}
            color="primary"
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} /> : "Confirmer"}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={openDepositHtg}
        // onClose={handleCloseDepositHtg}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">
          Deposit HTG : {row.full_name} !
        </DialogTitle>
        <DialogContent>
          <Stack spacing={2} sx={{ pt: 2 }}>
            <TextField
              value={amount}
              onChange={(event) => setAmount(event.target.value)}
              placeholder="Enter amount"
            />
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleCloseDepositHtg}
            color="primary"
            disabled={loading}
          >
            Annuler
          </Button>
          <Button
            onClick={() => handleDepositHtg(row.uid)}
            color="primary"
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} /> : "Confirmer"}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={openWithdraw}
        // onClose={handleCloseWithdraw}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">
          Withdraw : {row.full_name} !
        </DialogTitle>
        <DialogContent>
          <Stack spacing={2} sx={{ pt: 2 }}>
            <TextField
              value={amount}
              onChange={(event) => setAmount(event.target.value)}
              placeholder="Enter amount"
            />
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleCloseWithdraw}
            color="primary"
            disabled={loading}
          >
            Annuler
          </Button>
          <Button
            onClick={() => handleWithdraw(row.uid)}
            color="primary"
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} /> : "Confirmer"}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={openWithdrawHtg}
        // onClose={handleCloseWithdrawHtg}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">
          Withdraw HTG : {row.full_name} !
        </DialogTitle>
        <DialogContent>
          <Stack spacing={2} sx={{ pt: 2 }}>
            <TextField
              value={amount}
              onChange={(event) => setAmount(event.target.value)}
              placeholder="Enter amount"
            />
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleCloseWithdrawHtg}
            color="primary"
            disabled={loading}
          >
            Annuler
          </Button>
          <Button
            onClick={() => handleWithdrawHtg(row.uid)}
            color="primary"
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} /> : "Confirmer"}
          </Button>
        </DialogActions>
      </Dialog>
    </TableRow>
  );
}
